import React, { FC, useState } from 'react';

import { addComment } from '../../api/comments';

import './Form.scss';

type Props = {
  isCommentAdded: boolean,
  setCommentAdded: (x: boolean) => void,
};

const Form: FC<Props> = ({ isCommentAdded, setCommentAdded }) => {
  const [username, setUserName] = useState('');
  const [commentText, setCommentText] = useState('');

  const handleChangeSetName = (e: React.ChangeEvent<HTMLInputElement>) => {
    const fieldValue = e.target.value;

    setUserName(fieldValue);
  };

  const handleChangeSetText = (e: React.ChangeEvent<HTMLTextAreaElement>) => {
    const fieldValue = e.target.value;

    setCommentText(fieldValue);
  };

  const handleSubmit = async (e: React.ChangeEvent<HTMLFormElement>) => {
    e.preventDefault();
    await addComment(30, username, commentText);

    setCommentAdded(!isCommentAdded);

    setUserName('');
    setCommentText('');
  };

  return (
    <form className="Form" onSubmit={handleSubmit}>
      <input
        type="text"
        className="input is-primary"
        name="name"
        placeholder="Type name"
        required
        value={username}
        onChange={handleChangeSetName}
      />

      <textarea
        className="textarea is-primary"
        name="comment"
        placeholder="Type comment"
        required
        value={commentText}
        onChange={handleChangeSetText}
      />

      <button
        type="submit"
        className="button is-primary Form--button"
      >
        Add
      </button>
    </form>
  );
};

export default React.memo(Form);
